import * as d3 from 'd3'

export default class MultiFieldMapViewD3 {
    constructor(el, props) {
        this.props = props;

        this.svg = d3.select(el).append('svg')
            .attr("width", props.width)
            .attr("height", props.height);
        this.svg.style("background","url('/src/lib/components/coastline-world.png') no-repeat");
        this.svg.style("background-size","800px 400px");

        var polygon = this.svg.append('polygon')
            .attr('points', "50,50 200,50 250,100 250,150 20,50")
            .attr('stroke', '#f00')
            .attr('fill', 'none');
    }

    update(props) {
        this.props = props;
    }
}